const useStyles = makeStyles((theme) => ({
  container: {
    display: "flex",
    flexDirection: "column"
  },
  header: {
    display: "flex",
    backgroundColor: "lightgray",
    height: "64px",
    justifyContent: "space-between"
  },
  typingArea: {
    width: "80%",
    minHeight: "100px",
    resize: "none"
  }
}));

export default function App() {
  const [sentence, setSentence] = useState("");
  const [currIdx, setCurrIdx] = useState(0);
  const [score, setScore] = useState(0);
  const [userText, setUserText] = useState("");
  const [currChar, setCurrChar] = useState("");
  const [startTime, setStartTime] = useState("");
  const [endTime, setEndTime] = useState("");
  const [totalTime, setTotalTime] = useState(0);

  function getSentence() {
    let randomIndex = Math.floor(Math.random() * LINES.length);
    return LINES[randomIndex];
  }
  const handleStart = () => {
    const newSentence = getSentence();
    setSentence(newSentence);
    setStartTime(Date.now());
    console.log(Date.now());
    setUserText("");
  };

  function handleReset() {
    setSentence(getSentence());
    setScore(0);
    setUserText("");
  }

  function handleTyping(e) {
    setCurrChar(e.key);
  }

  function handleChange(e) {
    if (currChar === sentence.charAt(currIdx)) {
      setUserText(e.target.value);
      setCurrIdx((idx) => idx + 1);
    }
    if (currIdx >= sentence.length - 1) {
      const endTime = Date.now();
      let totalTime = (endTime - startTime) / 1000;
      setTotalTime(totalTime);
      setScore(Math.ceil(totalTime / sentence.length));
    }
  }

  const classes = useStyles();
  return (
    <div className={classes.container}>
      <div className={classes.header}>
        <button onClick={handleStart}>start</button>
        <h1>Speed Typing</h1>
        <button onClick={handleReset}>reset</button>
      </div>
      <div className={classes.sentence}>
        <p>{sentence} </p>
      </div>
      <div>
        <textarea
          className={classes.typingArea}
          value={userText}
          onKeyDown={handleTyping}
          onChange={handleChange}
        />
      </div>
      <div className={classes.result}>
        <p>CPS: {score} second per Charactor </p>
        <p>Total time: {totalTime} seconds </p>
      </div>
    </div>
  );
}
